import React from "react"
import styled from "styled-components"

const StyledMainContainer = styled.div`
  padding: 2em;
  margin: 0;
  list-style: none;
  display: block;
  width: 80%;
  margin: 0 auto;
`

export default function MainContainer({ children }) {
  return <StyledMainContainer>{children}</StyledMainContainer>
}
